  font-family: "PT Sans", sans-serif;
}

.home,
.contactus,
.products,
.services
{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  font-size: 4rem;
  color: white;
  
}

@font-face {
	font-family: mainFont;
	src: url(/IM_2006780/static/media/PressStart2P-Regular.5f45b521.ttf)
 }
 
 html {
  font-family:'mainFont',serif;}
.navbar  {
  height: 10vh;
  background: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
 
}
link{
  font-family: 'mainFont';
}
.navbar-logo {
  color: whitesmoke;
  text-decoration: none;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  width: 9rem;
  justify-content: space-between;
  font-family:'mainFont',serif;
}

.nav-items {
  width: 65vw;
  display: flex;
  justify-content: flex-end;
  margin-right: 2rem;
  
}

.nav-item {
  list-style: none;
  margin-left: 2rem;
  
}

.nav-item a {
  text-decoration: none;
  color: whitesmoke;
  font-size: 1.2rem;
  padding: 6px 16px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.2s all ease-out;
  
}

.nav-item a:hover {
  background: #008F11;
  
}

.nav-item a span {
  margin-left: 0.7rem;
  font-size: 1.3rem;
  font-family:'mainFont',serif;
}

.sidebar-toggle {
  width: 65vw;
  display: flex;
  justify-content: flex-end;
}

.sidebar-toggle-logo {
  font-size: 2.3rem;
  color: whitesmoke;
  cursor: pointer;
}

.sidebar {
  width: 16rem;
  background: #000000;
  height: 90vh;
  position: absolute;
  top: 10vh;
  left: -100%;
  transition: 0.3s all ease-in-out;
}

.sidebar.active {
  left: 0;
}

.sidebar-items {
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  transition: 0.2s all ease-out;
}

.sidebar-item {
  list-style: none;
  margin-top: 1rem;
  width: 100%;
  height: 3rem;
}

.sidebar-item a {
  text-decoration: none;
  color: whitesmoke;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s all ease-out;
}

.sidebar-item a span {
  margin-left: 1.5rem;
  font-size: 1.4rem;
}

.sidebar-item a:hover {
  background: #008F11;
}

.sidebar .btn {
  margin-top: 2.4rem;
  width: 100%;
}

@media screen and (max-width: 900px) {
  .sidebar.active {
    width: 5rem;
  }

  .sidebar-item a span {
    display: none;
  }
}

html {
    color: limegreen;
    background-color: black;
  }
  
  .blink {
    animation-name: blinkframes;
    animation-duration: 1s;
    animation-iteration-count: infinite;
  }
  
  @keyframes blinkframes {
    0% {
      color: black;
    }
    49% {
      color: black;
    }
    50% {
      color: limegreen;
    }
    100% {
      color: limegreen;
    }
  }

