	 box-sizing: border-box;
	 height: 100%;
	 background-color: #000;
	 
	 background-repeat: no-repeat;
	 background-size: cover;
	 font-family: 'Inconsolata', Helvetica, sans-serif;
	 font-size: 1.5rem;
	 color: lime
	 
}
 .noise {
	 pointer-events: none;
	 position: absolute;
	 width: 100%;
	 height: 100%;
	 
	 background-repeat: no-repeat;
	 background-size: cover;
	 z-index: -1;
	 opacity: 0.02;
}
 .overlay {
	 pointer-events: none;
	 position: absolute;
	 width: 100%;
	 height: 100%;
	 background: repeating-linear-gradient(180deg, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0) 100%);
	 background-size: auto 4px;
	 z-index: 1;
}
 .overlay::before {
	 content: "";
	 pointer-events: none;
	 position: absolute;
	 display: block;
	 top: 0;
	 left: 0;
	 right: 0;
	 bottom: 0;
	 width: 100%;
	 height: 100%;
	 background-image: linear-gradient(0deg, transparent 0%, rgba(138, 22, 122, 0.2) 2%, rgba(6, 78, 36, 0.8) 3%, rgba(5, 80, 34, 0.2) 3%, transparent 100%);
	 background-repeat: no-repeat;
	 animation: scan 3.5s linear 0s infinite;
}
 @keyframes scan {
	 0% {
		 background-position: 0 -100vh;
	}
	 35%, 100% {
		 background-position: 0 100vh;
	}
}
 .terminal {
	 box-sizing: inherit;
	 position: absolute;
	 height: 300px;
	 width: 100%;
	 max-width: 100%;
	 padding: 4rem;
	 text-transform: uppercase;
     background-color: #000000;
	 opacity: 50%;
     
}
 .output {
	 color: lime,
	 
}
 .output::before {
	 content: "> ";
}

 
 .errorcode {
	 color: white;
}
 
:root {
	 --d: 700ms;
	 --e: cubic-bezier(0.19, 1, 0.22, 1);
	 --font-sans: 'Rubik', sans-serif;
	 --font-serif: 'Cardo', serif;
}
 * {
	 box-sizing: border-box;
}
 html, body {
	 height: 100%;
}

 .page-content {
	 display: grid;
	 grid-gap: 1rem;
	 padding: 1rem;
	 max-width: 1024px;
	 margin: 0 auto;
	 font-family: 'Rubik', sans-serif;
	 font-family: var(--font-sans);
}
 @media (min-width: 600px) {
	 .page-content {
		 grid-template-columns: repeat(2, 1fr);
	}
}
 @media (min-width: 800px) {
	 .page-content {
		 grid-template-columns: repeat(4, 1fr);
	}
}
 .card {
	 position: relative;
	 display: flex;
	 align-items: flex-end;
	 overflow: hidden;
	 padding: 1rem;
	 width: 100%;
	 text-align: center;
	 color: whitesmoke;
	 background-color: whitesmoke;
	 box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.1), 0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.1), 0 16px 16px rgba(0, 0, 0, 0.1);
}
 @media (min-width: 600px) {
	 .card {
		 height: 350px;
	}
}
 .card:before {
	 content: '';
	 position: absolute;
	 top: 0;
	 left: 0;
	 width: 100%;
	 height: 110%;
	 background-size: cover;
	 background-position: 0 0;
	 transition: transform calc(700ms * 1.5) cubic-bezier(0.19, 1, 0.22, 1);
	 transition: transform calc(var(--d) * 1.5) var(--e);
	 pointer-events: none;
	 color: aqua;
}
 .card:after {
	 content: '';
	 display: block;
	 position: absolute;
	 top: 0;
	 left: 0;
	 width: 100%;
	 height: 200%;
	 	 pointer-events: none;
	 background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.009) 11.7%, rgba(0, 0, 0, 0.034) 22.1%, rgba(0, 0, 0, 0.072) 31.2%, rgba(0, 0, 0, 0.123) 39.4%, rgba(0, 0, 0, 0.182) 46.6%, rgba(0, 0, 0, 0.249) 53.1%, rgba(0, 0, 0, 0.320) 58.9%, rgba(0, 0, 0, 0.394) 64.3%, rgba(0, 0, 0, 0.468) 69.3%, rgba(0, 0, 0, 0.540) 74.1%, rgba(0, 0, 0, 0.607) 78.8%, rgba(0, 0, 0, 0.668) 83.6%, rgba(0, 0, 0, 0.721) 88.7%, rgba(0, 0, 0, 0.762) 94.1%, rgba(0, 0, 0, 0.790) 100%);
	 transform: translateY(-50%);
	 transition: transform calc(700ms * 2) cubic-bezier(0.19, 1, 0.22, 1);
	 transition: transform calc(var(--d) * 2) var(--e);
}
 .card:nth-child(1):before {
	 background-image: url("/IM_2006780/static/media/digital divide.5889a8e7.jpg");
}
 .card:nth-child(2):before {
	 background-image: url(/IM_2006780/static/media/c3.13a490b2.jpg);
}
